class Api {
  url: string;
  ajax: XMLHttpRequest;

  constructor(url: string) {
    this.url = url;
    this.ajax = new XMLHttpRequest();
  }

  protected getRequest<AjaxResponse>(): AjaxResponse {
    this.ajax.open("GET", this.url, false);
    this.ajax.send();

    return JSON.parse(this.ajax.response);
  }

  /*
    getRequest ë©”ì†Œë“œëŠ” ë¦¬í„´ê°’ì„ ë¯¸ì§€ì˜ íƒ€ì…ìœ¼ë¡œ ë‘ê³ , 
    í•˜ìœ„ í´ë˜ìŠ¤ì—ì„œ getRequest ë©”ì†Œë“œë¥¼ í˜¸ì¶œí•  ë•Œ ë¦¬í„´ê°’ì˜ íƒ€ì…ì„ ì§€ì •í•´ì¤„ ê²ƒì´ê¸° ë•Œë¬¸ì— ì™¸ë¶€ì—ì„œëŠ” ì‚¬ìš©ë˜ë©´ ì•ˆëœë‹¤. 
    ê·¸ëŸ¬ë‹ˆê¹Œ í•˜ìœ„ í´ë˜ìŠ¤ë¥¼ ì‚¬ìš©í•˜ëŠ” ì¸¡ì—ì„œ 
    const api = new NewsFeedApi(NEWS_URL);
    api.getRequest(); 
    ì´ëŸ° ì‹ìœ¼ë¡œ ì´ ë©”ì†Œë“œì— ì ‘ê·¼í•  ìˆ˜ ì—†ë„ë¡ ë§‰ì•„ì•¼ í•œë‹¤.
    ì´ë ‡ê²Œ ì¸ìŠ¤í„´ìŠ¤ì— ë…¸ì¶œì‹œí‚¤ê³  ì‹¶ì§€ ì•Šì€ ì†ì„±ì´ë‚˜ ë©”ì†Œë“œê°€ ìˆìœ¼ë©´ ì•ì— protected í‚¤ì›Œë“œë¥¼ ë¶™ì—¬ì¤€ë‹¤.
  */
}

class NewsFeedApi extends Api {
  getData(): NewsFeed[] {
    return this.getRequest<NewsFeed[]>();
  }
}

class NewsDetailApi extends Api {
  getData(): NewsDetail {
    return this.getRequest<NewsDetail>();
  }
}

