  // ë”ì—ì„œ rootë¥¼ ì°¾ì§€ ëª»í•  ê²½ìš° containerëŠ” nullì´ ë  ìˆ˜ë„ ìˆìœ¼ë¯€ë¡œ
  // containerê°€ ì¡´ì¬í•  ë•Œë§Œ innerHTML ì†ì„±ì„ ì‚¬ìš©í•˜ë„ë¡ "íƒ€ì… ê°€ë“œ" ì½”ë“œë¥¼ ê¼­ ì‘ì„±í•˜ê¸°
